$col-danger-light4: lighten($col-danger, 50%);
$col-danger-dark: darken($col-danger, 20%);
$col-danger-dark2: darken($col-danger, 30%);

$col-warning: #E10E55;
$col-warning-light: lighten($col-warning, 20%);
$col-warning-light2: lighten($col-warning, 30%);
