$col-structure-light2: lighten($col-structure, 30%);
$col-structure-light3: lighten($col-structure, 40%);
$col-structure-light4: lighten($col-structure, 50%);
$col-structure-light5: lighten($col-structure, 60%);
$col-structure-light6: lighten($col-structure, 70%);
