$col-light: #fff;
$col-light-dark: #f8f9fa;
$col-light-dark2: #e9eaeb;
$col-text: #20133C;
$col-text-light: lighten($col-structure, 80%);

$col-linkedin: #2867B2;

@mixin btn-colors($colP, $colD, $colL, $colT, $colTA) {
  background-color: $colP !important;
  color: $colT !important;

  &:hover, &:focus {
    background-color: $colD !important;
    color: $colTA !important;
  }

  &.active {
    background-color: $colL !important;
    color: $colTA !important;
  }

  &:disabled {
    background-color: $colP !important;
    color: $colT !important;
    opacity: 0.25;
  }
}

@mixin btn-icon-colors($colP, $colD, $colL) {
  color: $colP !important;
  background-color: transparent !important;

  &:hover, &:focus {
    color: $colD !important;
    background-color: transparent !important;
  }

  &.active {
    color: $colL !important;
    background-color: transparent !important;
  }

  &:disabled {
    color: $colP !important;
    opacity: 0.25;
    background-color: transparent !important;
  }
}

